.container {
    height: 75px;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px #999;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0.5rem 0;
    
    .content {
        width: 100%;
        max-width: 1200px;
        display: flex;
        flex-direction: row;
        justify-content: 'center';
        align-items: 'center';

        .left, .right {
            flex: 1;
        }
        .center {
            flex: 3;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;

            .link {
                text-decoration: none;
                padding: 0.5rem;
                font-weight: bold;
                color: #666;

                &.selected { color: darkorange }
            }
        }
    }
}