.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .ingressButton, .registerButton {
        margin: 0 0.5rem;
        padding: 0.5rem;
        border: none;
        border-radius: 0.5rem;
        font-weight: bold;
        border: 2px solid darkorange;
    }
    .ingressButton {
        background-color: white;
        color: #333;
    }
    .registerButton {
        background-color: darkorange;
        color: white;
    }
    .logoutButton {
        margin: 0 0.5rem;
        padding: 0.5rem;
        border: none;
        border-radius: 0.5rem;
    }
}