.container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.25rem;
    overflow: hidden;
    box-shadow: 0 0 0.25rem #999;

    .input {
        flex: 1;
        padding: 1rem;
        border: none;
        outline: none;
    }

    .search {
        padding: 0.5rem;
    }
}