.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 200px;
    padding: 0.5rem;
    box-shadow: 1px 1px 0.5rem #999;
    border-radius: 0.5rem;
    margin: 0.5rem;

    > * {
        margin: 0.25rem 0;
    }

    .price {
        font-size: 1.2em;
        font-weight: bold;
    }

    .cartController {
        display: flex;
        flex-direction: row;

        label {
            flex: 1;
            text-align: center;
        }

        button {
            background-color: darkorange;
            border-radius: 0.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border: none;
            font-weight: bold;
            color: white;
            width: 30px;
            height: 30px;
            font-size: 20px;
        }
        button:disabled, button[disabled] {
            background-color: gray;
        }
    }
}