.container {
    display: flex;
    flex-direction: column;
    background-color: darkorange;
    border-radius: 0.5rem;
    box-shadow: 1px 1px 5px #666;
    padding: 0.5rem;
    margin: 0.5rem 0;
    color: white;

    .item {
        display: flex;
        flex-direction: row;
        padding: 0.25rem 0;
        margin: 0.25rem 0;
        border-bottom: 1px solid white;

        b, label { margin: 0 0.25rem }

        b { flex: 1 }
    }

    button {
        background-color: white;
        border: 1px solid white;
        border-radius: 0.5rem;
        padding: 0.5rem;
        font-weight: bold;
    }

    .header {
        display: flex;
        flex-direction: row;

        label {
            text-align: center;
            flex: 1;
            font-size: 1.5em;
            font-weight: bold;
        }

        button {
            background-color: transparent;
            padding: 0;
            border: none;
        }
    }

    .content {
        display: flex;
        flex-direction: row;
        flex: 1;

        .card {
            padding: 0.5rem;
            flex: 1;
            display: flex;
            flex-direction: column;
            max-height: 200px;
            
            .title {
                font-weight: bold;
                text-align: center;
            }

            .cardContent {
                flex: 1;
                display: flex;
                flex-direction: column;
                overflow-y: auto;
            }
        }

        input {
            background-color: transparent;
            padding: 0.25rem;
            margin: 0.25rem 0;
            border: 1px solid #666;
            border-radius: 0.5rem;
            color: white;
        }
    }

    .total {
        font-size: 1.5em;
        margin: 1rem 0;
        font-weight: bold;
    }
}