.container {
    display: flex;
    flex-direction: column;

    .messages {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 0 0.5rem;
        overflow-y: auto;
        max-height: 80vh;
        margin-bottom: 0.5rem;

        .message {
            display: flex;
            flex-direction: column;
            background-color: white;
            border-radius: 0.5rem;
            box-shadow: 1px 1px 3px #333;
            margin: 0.5rem 0;
            padding: 0.5rem;
            width: 66%;
            box-sizing: border-box;

            &.client {
                margin-left: 33%;
                text-align: right;
            }

            .name {
                font-weight: bold;
                font-size: 0.9em;
                text-decoration: underline;
            }

            .body {
                margin: 0.5rem 0;
            }
        }
    }

    .form {
        background-color: darkorange;
        border-radius: 0.5rem;
        padding: 0.5rem;
        box-shadow: 1px 1px 5px #666;
        display: flex;
        flex-direction: row;

        input {
            flex: 1;
            background-color: transparent;
            border: none;
            padding: 0.5rem;
            color: white;
            font-weight: bold;
            outline: none;
        }

        button {
            background-color: white;
            border-radius: 0.5rem;
            border: none;
            font-weight: bold;
        }
    }
}