.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .form {
        display: flex;
        flex-direction: column;

        input {
            padding: 0.5rem;
            margin: 0.5rem 0;
            border-radius: 0.5rem;
            border: 1px solid #666;
        }
        button {
            margin: 0 0.5rem;
            padding: 0.5rem;
            border: none;
            border-radius: 0.5rem;
            font-weight: bold;
            border: 2px solid darkorange;
            background-color: darkorange;
            color: white;
        }
    }
    .back {
        margin: 1rem 0;
        background-color: transparent;
        border: none;
    }
}